.noselection, button {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

button {
  outline: none;
  border: var(--border-width) solid var(--button-border-color);
  border-radius: var(--border-radius-hard);
  background-color: var(--button-background-color);
  color: var(--button-color);
  padding: 1rem 2rem;
  font-size: 1.25rem;
  cursor: pointer;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}
button img {
  height: 1rem;
}

button.selected, .button.selected {
  font-weight: 600 !important;
  color: var(--base-blue-color-lightened);
  border-color: var(--base-blue-color-lightened) !important;
}

.flex-column-wrap, .flex-column, .flex-row-wrap, .flex-row, .flex {
  display: flex;
}

.flex-row-wrap, .flex-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.flex-column-wrap, .flex-column {
  flex-direction: column;
}

.flex-column-wrap, .flex-row-wrap {
  flex-wrap: wrap;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--color-lightened-3);
  font-family: "Roboto", serif;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
}

body * {
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

*::-webkit-scrollbar {
  width: 0.5rem;
}

*::-webkit-scrollbar-track {
  background: var(--color-lightened-6);
  margin: 1.5rem 0;
  border-radius: var(--border-radius);
}

*::-webkit-scrollbar-thumb {
  background-color: var(--color-lightened-3);
  border-radius: var(--border-radius);
  border: var(--border-width) solid var(--color-lightened-3);
}

*::-webkit-scrollbar-corner {
  background: rgba(0, 0, 0, 0);
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
