html,
body {
  background-color: var(--base-background-color);
  overflow: scroll;
  overflow-y: auto !important;
  overflow-x: hidden !important;
}

form {
  width: 100%;
  display: contents;
}

input {
  text-align: left;
  height: 4.5rem;
  padding: 1px 1px 1px 2em;
  box-sizing: border-box;
  border-radius: var(--border-radius-hard);
  background-color: var(--base-end-color);
}
input:focus {
  border-color: var(--base-blue-color) !important;
}
input:disabled {
  filter: opacity(0.85);
}
input::placeholder {
  color: var(--base-font-color);
}
input:autofill, input:autofill:hover, input:autofill:focus {
  box-shadow: 0 0 0 1000px var(--base-end-color) inset !important;
  color: var(--base-font-color) !important;
  -webkit-text-fill-color: var(--base-font-color) !important;
  background-clip: content-box !important;
  padding: 1px 1px 1px 2em !important;
  -webkit-appearance: none !important;
}

form {
  width: 100%;
  display: contents;
}

h1 {
  font-weight: 500;
}

textarea {
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  background-color: var(--base-end-color);
}

br {
  height: 0;
  margin: 0.625rem 0;
}

.button {
  cursor: pointer;
}
button:hover {
  filter: saturate(1.5);
}

.block {
  display: block;
}

.inline {
